<template>
  <div class="update-birthday">
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title=""
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="$emit('close')"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user.js'
import dayjs from 'dayjs'
export default {
  name: 'UpdateBirthday',
  props: {
    birthday: {
      type: [String, Number],
      required: true
    }
  },
  components: {},
  data() {
    return {
      minDate: new Date(1990, 5, 10),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(this.birthday)
    }
  },
  computed: {},
  watch: {},
  methods: {
    async onConfirm() {
      this.$toast.loading({
        message: '',
        forbidclick: true
      })

      //  
      const date = dayjs(this.currentDate).format('YYYY-MM-DD')
      await updateUserProfile({
        birthday: date
        // birthday: `${this.currentDate.getFullYear()}-${
        //   this.currentDate.getMonth() + 1
        // }-${this.currentDate.getDate}`
      })
      console.log(date)
      //   birthday
      this.$emit('update:birthday', date)

      // 
      this.$emit('close')

      this.$toast.success('')
    }
  },
  beforeCreate() {},
  // -this
  created() {},
  beforeMount() {},
  // -DOM
  mounted() {},
  beforeUpdate() {},
  // -
  updated() {},
  // keep-alive
  activated() {},
  // keep-alive
  deactivated() {},
  // -,
  beforeDestroy() {},
  // -
  destroyed() {}
}
</script>
