<template>
  <div class="update-data">
    <van-nav-bar
      class="app-nav-bar"
      title=""
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />
    <div class="data-wrap">
      <van-field
        v-model="localName"
        rows="2"
        autosize
        type="textarea"
        maxlength="10"
        placeholder=""
        show-word-limit
      />
    </div>
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user.js'
export default {
  name: 'Update-data',
  props: {
    value: {
      type: String,
      required: true
    }
  },
  components: {},
  data() {
    return {
      localName: this.value
    }
  },
  computed: {},
  watch: {},
  methods: {
    async onConfirm() {
      this.$toast.loading({
        message: '',
        forbidclick: true
      })

      try {
        //  
        await updateUserProfile({
          name: this.localName
        })

        //   namm
        // this.$emit('update-name', this.localName)
        this.$emit('input', this.localName)

        // 
        this.$emit('close')

        this.$toast.success('')
      } catch (error) {
        if (error && error.response && error.response.status === 409) {
          this.$toast.fail('')
        }
      }
    }
  },
  // -this
  created() {},
  // -DOM
  mounted() {},
  // -
  beforeCreate() {},
  // -
  beforeMount() {},
  // -
  beforeUpdate() {},
  // -
  updated() {},
  // -,
  beforeDestroy() {},
  // -
  activated() {}
}
</script>
<style lang='less' scoped>
/* @import url(); css */
.update-data {
  /deep/.app-nav-bar {
    color: #000000 !important;
    background: #ffffff !important;
    .van-nav-bar__title {
      color: #000000 !important;
    }
  }
  .data-wrap {
    padding: 10px;
  }
}
</style>