<template>
  <!-- :default-index="defaultIndex"  -->
  <div class="update-gender">
    <van-picker
      show-toolbar
      :columns="columns"
      :default-index="defaultIndex"
      @cancel="$emit('close')"
      @confirm="onConfirm"
      @change="onChange"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user.js'

export default {
  name: 'UpdateGender',
  props: {
    gender: {
      type: Number
    }
  },
  components: {},
  data() {
    return {
      columns: ['', ''],
      defaultIndex: this.gender
    }
  },
  computed: {},
  watch: {},
  methods: {
    // vant-picker @change 
    onChange(picker, value, index) {
      this.defaultIndex = index
    },
    async onConfirm() {
      this.$toast.loading({
        message: '',
        forbidclick: true
      })

      //  
      await updateUserProfile({
        gender: this.defaultIndex
      })

      //   namm
      // this.$emit('update-name', this.localName)
      this.$emit('update:gender', this.defaultIndex)

      // 
      this.$emit('close')

      this.$toast.success('')
    }
  },
  beforeCreate() {},
  // -this
  created() {},
  beforeMount() {},
  // -DOM
  mounted() {},
  beforeUpdate() {},
  // -
  updated() {},
  // keep-alive
  activated() {},
  // keep-alive
  deactivated() {},
  // -,
  beforeDestroy() {},
  // -
  destroyed() {}
}
</script>
