<template>
  <div class="update-photo">
    <van-nav-bar
      class="app-nav-bar"
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />
    <img class="preview-img" :src="previewImage" ref="image" />
  </div>
</template>

<script>
import { updateUserPhoto } from '@/api/user.js'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
export default {
  name: 'UpdatePhoto',
  props: {
    fileObj: {
      type: [Object, String, File],
      required: true
    }
  },
  components: {},
  data() {
    return {
      previewImage: window.URL.createObjectURL(this.fileObj),
      cropper: null
    }
  },
  computed: {},
  watch: {},
  methods: {
    getCroppedCanvas() {
      return new Promise((resolve) => {
        this.cropper.getCroppedCanvas().toBlob((file) => {
          resolve(file)
        })
      })
    },
    async onConfirm() {
      this.$toast.loading({
        message: '',
        forbidclick: true
      })
      // FormData  key/value 
      //  Content-Type:multipart/form-data  FormData 

      //  
      const file = await this.getCroppedCanvas()
      // 1.  FormData  
      const fd = new FormData()
      // 2.  1  2 
      // fd.append('photo', this.fileObj)
      fd.append('photo', file)
      // 3.   FormData 
      // await updateUserPhoto(fd)
      await updateUserPhoto(fd)

      // 4. 
      this.$emit('update-fileObj', window.URL.createObjectURL(file))

      this.$toast.success('')

      this.$emit('close')
    }
  },
  beforeCreate() {},
  // -this
  created() {},
  beforeMount() {},
  // -DOM
  mounted() {
    // 1. DOM VueDOM refs
    const image = this.$refs.image
    this.cropper = new Cropper(image, {
      viewMode: 1,
      dragMode: 'move', // 
      aspectRatio: 1, // 
      cropBoxMovable: false, // 
      cropBoxResizable: false, // 
      background: false, // 
      movable: true // 
    })
  },
  beforeUpdate() {},
  // -
  updated() {},
  // keep-alive
  activated() {},
  // keep-alive
  deactivated() {},
  // -,
  beforeDestroy() {},
  // -
  destroyed() {}
}
</script>
<style lang='less' scoped>
/* @import url(); css */
.update-photo {
  height: 100vh;
  width: 100vw;
  /deep/.app-nav-bar {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    color: #000000 !important;
    background: #ffffff !important;
    .van-nav-bar__title {
      color: #000000 !important;
    }
  }
  .preview-img {
    display: block;
    max-width: 100%;
  }
}
</style>