<template>
  <div class="user-profile">
    <van-nav-bar
      class="app-nav-bar"
      title=""
      left-text
      left-arrow
      @click-left="$router.back()"
    />
    <!-- accept 'image/*'  -->
    <input
      type="file"
      ref="file"
      hidden
      accept="image/*"
      @change="onFileChange"
    />
    <van-cell center title="" is-link @click="$refs.file.click()">
      <van-image round fit="cover" width="36" height="36" :src="user.photo" />
    </van-cell>
    <van-cell
      center
      title=""
      is-link
      :value="user.name"
      @click="isShowPopup = true"
    ></van-cell>
    <van-cell
      center
      title=""
      is-link
      :value="user.gender == 0 ? '' : ''"
      @click="isGenderPopup = true"
    ></van-cell>
    <van-cell
      center
      title=""
      is-link
      :value="user.birthday"
      @click="isBirthdayPopup = true"
    ></van-cell>
    <!--  -->
    <van-popup
      v-model="isShowPopup"
      position="bottom"
      :style="{ height: '100%' }"
    >
      <!--  
      :name="user.name"
      @update-name="user.name = $event" 
          v-model="user.name"
           === :value="user.name" @input="user.name=$event"
            $emit('input',xxx) ''value''
        -->
      <!--  v-model  .sync 
      :gender="user.gender"
      @update-gender="user.gender = $event"  
          :gender.sync="user.gender" props:{} @click="$emit('update:abc', )
       https://cn.vuejs.org/v2/guide/components-custom-events.html#sync-%E4%BF%AE%E9%A5%B0%E7%AC%A6
      -->
      <update-data
        v-if="isShowPopup"
        v-model="user.name"
        @close="isShowPopup = !isShowPopup"
      ></update-data>
    </van-popup>

    <!--  -->
    <van-popup v-model="isGenderPopup" position="bottom">
      <update-gender
        v-if="isGenderPopup"
        :gender.sync="user.gender"
        @close="isGenderPopup = !isGenderPopup"
      ></update-gender>
    </van-popup>
    <!--  -->
    <van-popup v-model="isBirthdayPopup" position="bottom">
      <update-birthday
        v-if="isBirthdayPopup"
        :birthday.sync="user.birthday"
        @close="isBirthdayPopup = !isBirthdayPopup"
      ></update-birthday>
    </van-popup>
    <!--  -->
    <van-popup v-model="isPhotoPopup" position="bottom" style="height: 100%">
      <update-photo
        v-if="isPhotoPopup"
        :fileObj="fileObj"
        @close="isPhotoPopup = !isPhotoPopup"
        @update-fileObj="user.photo = $event"
      ></update-photo>
    </van-popup>
  </div>
</template>

<script>
import { getUserProfile } from '@/api/user.js'
import UpdateData from './component/update-data'
import UpdateGender from './component/update-gender.vue'
import UpdateBirthday from './component/update-birthday.vue'
import UpdatePhoto from './component/update-photo.vue'
export default {
  name: 'UserProfile',
  props: {},
  components: { UpdateData, UpdateGender, UpdateBirthday, UpdatePhoto },
  data() {
    return {
      user: {}, // 
      isShowPopup: false, // 
      isGenderPopup: false, // 
      isBirthdayPopup: false, // 
      isPhotoPopup: false, // 
      fileObj: null
    }
  },
  computed: {},
  watch: {},
  methods: {
    async loadUserProfile() {
      const { data } = await getUserProfile()
      //  user 
      this.user = data.data
    },

    // 
    onFileChange() {
      // 1. Blob 
      // const blob = window.URL.createObjectURL(this.$refs.file.files[0])
      const file = this.$refs.file.files[0]

      this.fileObj = file

      // 2. 
      this.isPhotoPopup = true
      //  value   onchange 
      this.$refs.file.value = ''
    }
  },
  // -this
  created() {
    this.loadUserProfile()
  },
  // -DOM
  mounted() {},
  // -
  beforeCreate() {},
  // -
  beforeMount() {},
  // -
  beforeUpdate() {},
  // -
  updated() {},
  // -,
  beforeDestroy() {},
  // -
  activated() {}
}
</script>
<style lang='less' scoped>
/* @import url(); css */
.van-popup {
  background: #f5f7f9;
}
</style>